import React from 'react'

const UpvotesSection = ({ articleName, upvtes, setArticleInfo }) => {
    const upvoteArticle = async () => {
        const result = await fetch(`/api/articles/${articleName}/upvte`, {
            method: 'post',
        })
        const body = await result.json();
        setArticleInfo(body);
    }

    return (
        <div id="upvotes-section">
            <button onClick={() => upvoteArticle()}>Add Upvote</button>
            <p>This post has been upvoted {upvtes} times</p>
        </div>
    )
}

export default UpvotesSection
